<template>
  <div class="page_wrap">
    <section class="password_wrap" v-if="false" ><span class="pwd_txt">password:</span><input class="pwd_input_area" type="text " v-model="password" /></section>

    <!-- 消息窗口 -->
    <section class="chat_wrap" v-if="filterChatList.length > 0">
      <div class="msg_wrap" v-for="(item, index) in filterChatList" :key="index" :class="{isMe: item.__isMe}">
        <aside class="user_wrap" v-if="!item.__isMe">{{ item.name }}</aside>
        <aside class="msg" :class="{isMe: item.__isMe}">{{ item.msg }}</aside>
        <aside class="user_wrap" v-if="item.__isMe">{{ item.name }}</aside>
      </div>
    </section>

    <section class="input_wrap">
      <input class="input_area" @keydown.enter="doClick" type="text " v-model="curContent" />
      <aside class="btn_send" @click="doClick">send</aside>
    </section>
  </div>
</template>

<script>
import io from "socket.io-client";
const socket = io("ws://106.12.161.139:3000");

export default {
  data: function() {
    return {
      password: "",
      curContent: "",
      userInfo: {},
      userList: [
        {
          uid: "007525639943605444",
          name: "德龙"
        },
        {
          uid: "7980809627105447",
          name: "feifei"
        }
      ],
      chatList: [
        {
          uid: "jjljlkjljl",
          name: "龙",
          msg: "开跌？"
        }
      ]
    };
  },
  computed: {
    filterChatList() {
      let filterChatList = [];
      let chatList = this.chatList;
      for (let i = 0; i < chatList.length; i++) {
        let __isMe = false;
        if (chatList[i].uid === this.userInfo.uid) {
          __isMe = true;
        }

        filterChatList.push({...chatList[i], __isMe});
      }
      return filterChatList;
    }
  },
  created() {
    if (!this.userInfo.uid) {
      let userInfo = window.localStorage.getItem("uid");
      console.log("[log]===>localStorage.getItem", userInfo);

      if (userInfo) {
        let [uid, name] = userInfo.split("-");
        this.userInfo = {uid, name};
      } else {
        console.log("[log]===> 没有用户信息，请输入");
      }
    } else {
      console.log("[log]===> 已经有用户信息了");
    }
  },
  mounted() {
    socket.on("connect", () => {
      socket.on("sendMsg", msg => {
        this.chatList.push(msg);
      });
    });
  },
  methods: {
    doClick() {
      if (!this.curContent) {
        console.log("sorry, you aren't able to send empty msg");
        return;
      }

      let msgObj = {
        msg: this.curContent,
        ...this.userInfo
      };

      this.curContent = "";

      socket.emit("sendMsg", msgObj);
    }
  }
};
</script>
