    max-width: 4rem;

    &.isMe::after {
      content: "";
      position: absolute;
      right: -0.22rem;
      left: inherit;
      top: 0.26rem;
      width: 0;
      height: 0;
      border: 0.12rem solid rgba(0, 0, 0, 0);
      border-left-color: #fefefe;
    }

    &::after {
      content: "";
      position: absolute;
      left: -0.22rem;
